@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Rubik", sans-serif;
}

:root {
	/* Layout */
	--padding_global: 18px;
	/* Colors */
	--skyBlue: rgb(0, 195, 255);
	--navBackground: rgba(0, 0, 0);
	--white: rgb(255, 255, 255);
	--lightBlue: rgb(240, 248, 255);
	--blueGrey: rgb(102, 153, 204);
	--lightGrey: rgba(211, 211, 211, 0.4);
	--blue: rgb(5, 92, 157);
  --btnBlue: rgb(103, 159, 211);
}

.header__container {
	display: flex;
	flex-wrap: wrap;
	padding: var(--padding_global);
	position: relative;
	box-shadow: 3px 8px 30px rgb(100, 149, 237);
}

.navIcon {
	width: 100%;
	height: 35px;
	display: flex;
	justify-content: space-between;
}

.logo__mobile {
	width: 130px;
	height: 35px;
}

.menuIcon {
	cursor: pointer;
}

.navMenu__active {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	margin-top: 50px;
	width: 100%;
	height: 80vh;
	background-color: var(--navBackground);
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

.navMenu {
	display: none;
}

.navMenu__list,
.loginBtn {
	text-align: center;
}

.navMenu__list {
	list-style: none;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	height: 27vh;
}

.navLink {
	text-decoration: none;
	color: var(--white);
	position: relative;
}

.navLink::before {
	content: "";
	position: absolute;
	width: 100%;
	height: 2px;
	background-color: var(--white);
	left: 0;
	bottom: -3px;
	transform: scaleX(0);
	transition: all 0.5s ease;
	transform-origin: center;
}

.navLink:hover::before {
	transform: scaleX(1);
}

.loginBtn {
	margin: 17px 0;
}

.content__container {
	width: 100%;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding: var(--padding_global) var(--padding_global) 100px
		var(--padding_global);
	background: linear-gradient(180deg, var(--skyBlue), var(--lightBlue) 70%);
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
}

.welcome__text {
	margin: 100px 0 150px 0;
}

.welcome__text > h1 {
	font-size: 43px;
	margin-bottom: 25px;
}

.welcome__text > h1,
.welcome__text > p {
	text-align: center;
}

.product__section {
	padding: 0 10px;
}

.product__container {
	text-align: center;
	display: flex;
	flex-direction: column;
	width: 100%;
}

.productImg {
	object-fit: contain;
	min-height: 50px;
	min-width: 50px;
}

.product {
	background-color: var(--white);
	margin: 10px 0;
	padding: 50px 0 40px 0;
	width: 100%;
	box-shadow: 3px 8px 30px rgb(100, 149, 237);
}

.product__info {
	padding: 0 20px;
	margin-top: 50px;
	width: 100%;
}

.title {
	margin-bottom: 20px;
}

.comment__container {
	padding: 28px 28px 100px 28px;
	background: linear-gradient(180deg, var(--lightBlue) 30%, var(--skyBlue));
}

.comment__box {
	background-color: var(--white);
	padding: 25px;
	box-shadow: 3px 8px 30px rgb(100, 149, 237);
}

.comment__box > h2 {
	margin-bottom: 57px;
	color: var(--blueGrey);
}

.comment__info {
	margin-bottom: 40px;
}

.name {
	margin-bottom: 13px;
	color: var(--blue);
}

.email {
	font-weight: 400;
	font-size: 12px;
}

.comment {
	padding: 20px;
	border: 1px solid;
	background-color: var(--lightGrey);
}

.likeBtn {
	color: var(--btnBlue);
	margin-top: 10px;
}

.thumbsup,
.thumbsdown {
	cursor: pointer;
}

.endCommentMsg {
	text-align: center;
}

.showMoreBtn {
	border: none;
	background: transparent;
	cursor: pointer;
	text-decoration: underline;
	color: var(--blueGrey);
}

/* Mobile Padding for Content section */
@media screen and (max-width: 500px) {
	.welcome__text {
		padding: 0 40px;
	}
}

/* Desktop, Tablet */
@media screen and (min-width: 1000px) {
	/* Header */
	.header__container {
		flex-wrap: nowrap;
		justify-content: space-between;
	}

	.navIcon {
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
		justify-content: flex-start;
	}

	.logo__mobile {
		width: 180.64px;
		height: 50px;
		margin-top: -2px;
	}

	.menuIcon {
		display: none;
	}

	.navMenu {
		display: flex;
	}

	.navMenu__active {
		display: none;
	}

	.navMenu__list {
		text-align: left;
		flex-direction: row;
		width: 50%;
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		margin-top: 16px;
	}

	.navLink {
		color: var(--skyBlue);
	}

	.navLink::before {
		background-color: var(--skyBlue);
	}

	.loginBtn {
		margin: 0;
	}

	/* Content */
	.content__container {
		padding: var(--padding_global) 100px 130px 100px;
	}

	.welcome__text {
		margin-bottom: 200px;
	}

	.welcome__text > h1 {
		font-size: 60px;
	}

	.welcome__text > p {
		font-size: 20px;
	}

	/* Product */
	.product__container {
		flex-direction: row;
	}

	.product {
		margin: 0 40px;
		padding: 100px 0 60px 0;
	}

	.productImg {
		min-height: 90px;
		min-width: 60px;
	}

	/* Comment */
	.comment__container {
		padding: var(--padding_global) 150px 100px 150px;
		background: linear-gradient(180deg, var(--lightBlue) 30%, var(--skyBlue));
	}

	.comment__box {
		background-color: var(--white);
		padding: 70px;
	}

	.comment__box > h2 {
		margin-bottom: 80px;
	}

	.comment__info {
		margin-bottom: 40px;
	}

	.name {
		margin-bottom: 13px;
	}

	.email {
		font-weight: 400;
		font-size: 12px;
	}

	.comment {
		padding: 20px;
		border: 1px solid;
	}
}

